import { useState } from "react";
import Spinner from "react-bootstrap/Spinner";
import Modal from "react-bootstrap/Modal";
import placeholder from "./../placer.png";

const MainImage = ({
  isCreating,
  isMinting,
  title,
  description,
  medium,
  image,
  url,
  message,
  metaData,
  transactionHash,
  powerPoints,
}) => {
  const [lgShow, setLgShow] = useState(false);

  // console.log("transactionHashE:", transactionHash);

  const hashLink = `https://sepolia.etherscan.io/tx/${transactionHash}`;

  const points = powerPoints && powerPoints.slice(64, 66).toUpperCase()

  const renderImage = () => {
    if (!isCreating && !isMinting && image) {
      return (
        <div style={{ position: "relative", width: "350px", height: "350px",   }}>
          <div
            style={{
              backgroundImage: `url(${image})`,
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              width: "350px",
              height: "350px",
              display: "flex",
              flexDirection: "column",
             
         
              backgroundSize: "350px 350px",
              filter: "greyscale(50%)",
              justifyContent: "flex-start",
              alignItems: "start",
            }}
          >
         { points &&  <span
              style={{
                background: "red",
                color: "#fff",
                padding: "10px 15px",
                borderRadius: "5px",
                color: "white",
                margin: "5px",
               
              }}
            >
                 <a target="_blank" href={hashLink} >
               {points}
                </a>
            </span> }
          </div>

          {/* <img src={image} alt="AI generated image" />; */}
        </div>
      );
    } else if (isCreating || isMinting) {
      return (
        <div style={{ position: "relative", width: "350px", height: "350px" }}>
          <div
            style={{
              position: "absolute",
              top: "50%",
              left: "50%",
              transform: "translate(-50%, -50%)",
              width: "350px",
              height: "350px",
              display: "flex",
              flexDirection: "column",
              alignItems: "center",
              backgroundImage: `url(${image})`,
              backgroundSize: "350px 350px",
              filter: "greyscale(50%)",
              justifyContent: "center",
            }}
          >
            <Spinner
              animation="border"
              style={{ width: "5rem", height: "5rem", color: "#f8b817" }}
            />
            <span
              style={{
                background: "#4c46b6",
                color: "#fff",
                padding: "10px",
                borderRadius: "5px",
                boxShadow: "1px 1px gray",
                marginTop: "15px",
                
              }}
            >
              {message}
            </span>
          </div>
        </div>
      );
    } else {
      return (
        <div className="">
          <img src={placeholder} alt="AI generated art" width="350px" />
        </div>
      );
    }
  };

  return (
    <div>
      <Modal
        size="md"
        show={lgShow}
        onHide={() => setLgShow(false)}
        aria-labelledby="example-modal-sizes-title-sm"
       
      >
        <Modal.Header closeButton>
          <Modal.Title id="example-modal-sizes-title-sm">
            <div>
              <div className="title">
                {title}&nbsp;<em>"{description}"</em>,
              </div>
              <div>
                {/* &nbsp;&nbsp;{" "} */}
                <a target="_blank" href={metaData}>
                  Data
                </a>{" "}
                &nbsp;&nbsp;
                <a target="_blank" href={url}>
                  URL
                </a>
                &nbsp;&nbsp;&nbsp;&nbsp;
                <a target="_blank" href={hashLink}>
                  {transactionHash &&
                    transactionHash.slice(0, 4) +
                      "..." +
                      transactionHash.slice(62, 66)}
                </a>
              </div>
            </div>
          </Modal.Title>
        </Modal.Header>
        <Modal.Body>
          <div className="modal-image-container" style={{width: "350px"}}>{renderImage()} <p>In the sprawling bamboo forests of Sichuan, a timid panda named Pippin stumbled upon an ancient tome. Its pages whispered tales of valor and adventure, inspiring Pippin to become a comic book hero. Armed with his trusty bamboo staff, he donned a vibrant costume and became the Pandarian Guardian. By day, Pippin peacefully chomped on bamboo leaves, but by night, he leapt into action, battling evil forces threatening his beloved forest. With his unwavering determination and infectious charm, the Pandarian Guardian captured the hearts of readers worldwide, reminding them that even the meekest creatures can be mighty in their own way.</p></div>
         
        </Modal.Body>
      </Modal>

      <div className="image">
        <button className="icon" onClick={() => setLgShow(true)}>
          {renderImage()}
        </button>
      </div>

      <div className="main-image-text">
        <div className="title">
          {isCreating ? (
            "Your Artblock is being created..."
          ) : isMinting ? (
            "Your Artblock is being minted..."
          ) : !image ? (
            "Let's Create Something!"
          ) : (
            <p className="title"> 
              {title + ", " + "'" + description + "'"}

              {metaData && (
                <>
                  <a target="_blank" href={hashLink}>
                    &nbsp;&nbsp;&nbsp;Hash
                  </a>

                  <a target="_blank" href={metaData}>
                    &nbsp;&nbsp;&nbsp;Data
                  </a>
                  <a target="_blank" href={url}>
                    &nbsp;&nbsp;&nbsp;URL
                  </a>
                </>
              )}
            </p>
          )}
        </div>
      </div>
    </div>
  );
};

export default MainImage;
